import React from 'react';
import styled from 'styled-components'
import { Container, Row, Col } from 'reactstrap';
import { Card, CardImg, CardText, CardBody,
  CardTitle, CardSubtitle } from 'reactstrap';

import MyWebsite from "../images/my-website.png";
import Npm from "../images/npm.png";
import Xmail from "../images/Xmail.png";


const BodyTitle = styled.div`
  font-size: 16px;
  text-transform: uppercase
  font-weight: 500;
  text-align: center;
  color: black;
  font-family: 'proxima-nova', 'futura-pt space';
`;

const BodyText = styled.div`
  font-size: 14px;
  text-align: center;
  color: black;
  font-family: 'proxima-nova', 'futura-pt space';
`;

const TextStyle = styled.h1`
  font-family: 'proxima-nova', futura-pt space;
  font-size: 30px;
  color: rgb(0, 0, 0);
  text-align: center;
  padding: 12px 0px 50px 0px;
`
const Anchor = styled.a`
  color: black;

  &:hover, active {
    color: #867e7e;
  }

  .faGithub:hover {
      color: red;
  }
`

const ProjectsHome = () => (
  <div>
    <Container>
      <TextStyle>Personal Projects</TextStyle>
        <Row className="justify-content-center">
          <Col xs="12" sm="10" md="8" lg="4" xl="4" className="pb-3 pr-3 pl-3 pb-lg-0">
            <Card>
              <Anchor href="https://github.com/rebyoliveira/rebyoliveira.github.io" target="_blank">
                <CardImg top src={MyWebsite} alt="MyWebsite"/>
                <CardBody>
                  <CardTitle> <BodyTitle> Project #01: My website </BodyTitle> </CardTitle>
                  <CardSubtitle> <BodyText>Built using Gatsby, with Styled Component, Reactstrap and Material UI.</BodyText> </CardSubtitle>
                </CardBody>
              </Anchor>
            </Card>
          </Col>
            <Col xs="12" sm="10" md="8" lg="4" xl="4" className="pb-3 pr-3 pl-3 pb-lg-0">
            <Card>
              <Anchor href="https://rebyoliveira.github.io/xmail/" target="_blank">
                <CardImg top  src={Xmail} alt="Xmail"/>
                <CardBody>
                <CardTitle> <BodyTitle> Project #02: Xmail </BodyTitle> </CardTitle>
                <CardSubtitle> <BodyText> What's the best way to learn React/JSS/Redux? Create a Gmail clone! </BodyText> </CardSubtitle>
                </CardBody>
              </Anchor>
            </Card>
          </Col>
          <Col xs="12" sm="10" md="8" lg="4" xl="4" className="pb-3 pr-3 pl-3 pb-lg-0">
            <Card>
              <Anchor href="https://github.com/rebyoliveira/react-snippet-component" target="_blank">
                <CardImg top  src={Npm} alt="Npm"/>
                <CardBody>
                <CardTitle> <BodyTitle> Project #03: A package on NPM </BodyTitle> </CardTitle>
                <CardSubtitle> <BodyText> A snippet component package published on NPM, running a test with Travis.</BodyText> </CardSubtitle>
                </CardBody>
              </Anchor>
            </Card>
          </Col>
        </Row>
    </Container>
    <hr />
  </div>
);


export default ProjectsHome;



